@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;

  -webkit-font-smoothing: antialiased;
}

html, body, #root {
  height: 100%;
  min-height: 100%;
}

*, input, button, textarea {
  border: 0;
  outline: 0;

  font-family: 'Roboto', sans-serif;
}

:root {
  --primary: #FFF;
  --secondary: #000;
}
.layout {
  display: grid;

  grid-template-columns: auto;
  grid-template-rows: 12vh 76vh 12vh;

  grid-template-areas: 
    'navigationMenu'
    'mainContent'
    'footer'
  ;
}
.navigation-menu {
  grid-area: navigationMenu;
  display: flex;
  flex-direction: row;
  position: relative;

  align-items: center;
  justify-content: center;
}

.navigation-menu .left-links {
  position: absolute;
  left: 380px;
}

.navigation-menu .right-links {
  position: absolute;
  right: 250px;
}

.navigation-menu .logged-right-links {
  position: absolute;
  right: 110px;
}

.navigation-menu img {
  margin-top: -25px;
  height: 300px;
  width: 350px;
}

.navigation-menu a {
  text-decoration: none;
  color: #000;
  font-size: 24px;
  font-weight: 500;
  margin: 15px;
  transition: opacity 0.2s;
}

.navigation-menu a:hover {
  opacity: 0.5;
}

.navigation-menu .admin-panel {
  border-radius: 5px;
  padding: 5px;
  background: #0c71c3;
  color: #FFF;
}

.navigation-menu .book-online {
  border-radius: 5px;
  padding: 5px;
  background: #ff6257;
  color: #FFF;
}

.navigation-menu svg {
  margin-right: 10px;
}




.footer {
  grid-area: footer;
  display: flex;
  flex-direction: row;
  position: relative;

  align-items: center;
  justify-content: center;
}

.footer img {
  height: 30px;
  width: 30px;
}

.footer a {
  text-decoration: none;
  color: #000;
  font-size: 18px;
  font-weight: 500;
  margin-right: 12px;
  transition: opacity 0.2s;
}

.footer a:hover {
  opacity: 0.5;
}
.bookings-container {
  height: 100%;
  width: 100%;
  grid-area: mainContent;
  display: flex;
  justify-content: center;

  background: url(/static/media/pub-counter-cropped-blur.aec45872.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.bookings-content {
  width: 50%;
  margin-top: 30px;
  margin-bottom: 30px;

  padding: 30px;

  border-radius: 8px;

  align-items: center;
  justify-items: center;

  background: #ffffff;

  overflow: auto;
}

.bookings-content h1 {
  font-size: 38px;
}

.bookings-content h3 {
  font-weight: 400;
  margin-top: 35px;
  font-size: 24px;
}

.bookings-content a {
  text-decoration: none;
  color: #000;
  font-size: 24px;
  font-weight: 500;
  margin: 5px;
  transition: opacity 0.2s;
}

.bookings-content a:hover {
  opacity: 0.5;
}

.bookings-content svg {
  margin-left: 10px;
}


.bookings-content ul {
  margin-top: 25px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 50px;
  list-style: none;
  margin-bottom: 25px;
  align-items: center;
}

.bookings-content ul li {
  background: #fff5f5;
  padding: 24px;
  border-radius: 8px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: start;
}

.bookings-content ul li strong {
  font-size: 16px;
  margin: 7px;
}

.bookings-content ul li p {
  font-size: 17px;
}

.bookings-content ul li button {
  position: flex;
  background: transparent;
  max-width: 2px;
  border: 0;
  margin-top: 10px;
  margin-left: 0px;
  cursor: pointer;
}

.bookings-content ul li button:hover {
